/**
 * External Dependencies
 */
import classnames from 'classnames'; //npm install classnames --save

/**
 * WordPress Dependencies
 */
const { createHigherOrderComponent } = wp.compose;
const { Fragment } = wp.element;
const { InspectorControls } = wp.blockEditor;
const { PanelBody, PanelRow, SelectControl, Button, ButtonGroup } = wp.components;
const { addFilter } = wp.hooks;
const { __ } = wp.i18n;

//restrict to specific block names
const allowedBlocks = [ 'core/column' ];

const paddingLeftControlOptions = [
	{
		caption: __( 'Default' ),
		value: '',
	},
	{
		caption: __( 'S' ),
		value: 'padding-left-small',
	},
	{
		caption: __( 'M' ),
		value: 'padding-left-medium',
	},
	{
		caption: __( 'L' ),
		value: 'padding-left-large',
	},
	{
		caption: __( 'None' ),
		value: 'padding-left-none',
	},
];

const paddingRightControlOptions = [
	{
		caption: __( 'Default' ),
		value: '',
	},
	{
		caption: __( 'S' ),
		value: 'padding-right-small',
	},
	{
		caption: __( 'M' ),
		value: 'padding-right-medium',
	},
	{
		caption: __( 'L' ),
		value: 'padding-right-large',
	},
	{
		caption: __( 'None' ),
		value: 'padding-right-none',
	},
];

/**
 * Add custom attributes for spacings.
 *
 * @param {Object} settings Settings for the block.
 *
 * @return {Object} settings Modified settings.
 */
function addAttributes( settings ) {

	//check if object exists for old Gutenberg version compatibility
	//add allowedBlocks restriction
	if( typeof settings.attributes !== 'undefined' && allowedBlocks.includes( settings.name ) ){

		settings.attributes = Object.assign( settings.attributes, {
			paddingLeft:{
				type: 'text',
				default: false,
			},
			paddingRight:{
				type: 'text',
				default: false,
			}
		});

	}

	return settings;
}

/**
 * Add spacing controls on Advanced Block Panel.
 *
 * @param {function} BlockEdit Block edit component.
 *
 * @return {function} BlockEdit Modified block edit component.
 */

const withAdvancedControls = createHigherOrderComponent( ( BlockEdit ) => {
	return ( props ) => {

		const {
			name,
			attributes,
			setAttributes,
			toggled
		} = props;

		const {
			paddingLeft,
			paddingRight,
		} = attributes;

		return (
            <Fragment>
                <BlockEdit { ...props } />
				{ allowedBlocks.includes( name ) &&
                <InspectorControls>
					<PanelBody
						title={ __( 'Horizontal Paddings' ) }
						initialOpen={ false }
					>
					<PanelRow>
				      <strong>Left</strong> <ButtonGroup>
				            { paddingLeftControlOptions.map( item => (

				                 <Button
				                      key={item.value}
				                      isPrimary={attributes.paddingLeft == item.value}
									  value={ item.value }
								      onClick={ () => {
											setAttributes( {paddingLeft: item.value} );
											toggled === item.value ? null: item.value;
								        } }
								 >
		                        {item.caption}
				                  </Button>
								  ))}
				      </ButtonGroup>
					  </PanelRow>
					  <PanelRow>
				      <strong>Right</strong> <ButtonGroup>
				            { paddingRightControlOptions.map( item => (

				                 <Button
				                      key={item.value}
				                      isPrimary={attributes.paddingRight == item.value}
									  value={ item.value }
								      onClick={ () => {
											setAttributes( {paddingRight: item.value} );
											toggled === item.value ? null: item.value;
								        } }
								 >
		                        {item.caption}
				                  </Button>
								  ))}
				      </ButtonGroup>
					  </PanelRow>
					</PanelBody>
                </InspectorControls>
				}
            </Fragment>
		);
	};
}, 'withAdvancedControls');

/* Apply the spacings in the editor too */  // https://wordpress.stackexchange.com/questions/324091/
const withSpacingClasses = createHigherOrderComponent( ( BlockListBlock ) => {
    return ( props ) => {
        if ((props.attributes.paddingLeft) || (props.attributes.paddingRight))  {

			var spacingClasses = "";

	        if (props.attributes.paddingLeft){
				spacingClasses = spacingClasses+props.attributes.paddingLeft+' ';
			}
	        if (props.attributes.paddingRight){
				spacingClasses = spacingClasses+props.attributes.paddingRight+' ';
			}

			if (spacingClasses){
				spacingClasses = spacingClasses.substring(0, spacingClasses.length - 1);

	            //return <BlockListBlock { ...props } className={ "" + spacingClasses } />;
	            return <BlockListBlock { ...props } className={classnames( props.className, spacingClasses )} />;

			} else {
            	return <BlockListBlock {...props} />
        	}

        } else {
            return <BlockListBlock {...props} />
        }

    };
}, 'withClientIdClassName' );

wp.hooks.addFilter( 'editor.BlockListBlock', 'pwd-blocks/with-client-id-class-name', withSpacingClasses )


///

/**
 * Add custom element class in save element.
 *
 * @param {Object} extraProps     Block element.
 * @param {Object} blockType      Blocks object.
 * @param {Object} attributes     Blocks attributes.
 *
 * @return {Object} extraProps Modified block element.
 */

function applyExtraClass( extraProps, blockType, attributes ) {

	const {
		paddingLeft,
		paddingRight,
	} = attributes;

	/* check if attribute exists for old Gutenberg version compatibility*/
	/* add allowedBlocks restriction */
	if ( typeof paddingLeft !== 'undefined' && allowedBlocks.includes( blockType.name ) ) {
		extraProps.className = classnames( extraProps.className, paddingLeft );
	}

	if ( typeof paddingRight !== 'undefined' && allowedBlocks.includes( blockType.name ) ) {
		extraProps.className = classnames( extraProps.className, paddingRight );
	}

	return extraProps;
}

//add filters

addFilter(
	'blocks.registerBlockType',
	'pwd-blocks/custom-attributes',
	addAttributes
);

addFilter(
	'editor.BlockEdit',
	'pwd-blocks/custom-advanced-control',
	withAdvancedControls
);

addFilter(
	'blocks.getSaveContent.extraProps',
	'pwd-blocks/applyExtraClass',
	applyExtraClass
);


